import React, { Component } from 'react'
import cn from 'cn-decorator'

import QuestionsTopBar from './QuestionsTopBar'
import Chat from './Chat'


@cn('questions')
class Questions extends Component{
	constructor(props){
		super(props)
		this.state = {
			answers: [
			{
				1:'1 вариант',
				right: false
			}, 
			{
				2:'2 вариант',
				right: true
			}, 
			{
				3:'3 вариант',
				right: false
			}
			],
			variant: null,
		}
	}
	render(cn){
		setTimeout(() => {
			if(this.state.variant && this.state.answers[this.state.variant-1].right){
				this.props.history.push('/board/right-answer')
			}
			else if(this.state.variant  && !this.state.answers[this.state.variant-1].right){
				this.props.history.push('/board/wrong-answer')
			}else{
				this.props.history.push('/board/wrong-answer')
			}
		}, 10000)
		return(
			<div className={ cn() }>
				<QuestionsTopBar />
				<div className={ cn('question') }>Как называют людей которые слишком много спят?</div>
				{
					this.state.answers.map((value, index) =>{
						return(
							<div>
								<input 
									type="checkbox" 
									id={"variant"+(index+1)} 
									className={ cn('checkbox-answer') } 
									style={{'display':'none'}} 
									name="answers" 
									checked={this.state.variant == (index+1) ? true : false} 
									onClick={() => this.setState({...this.state, variant: (index+1)})}
								/>
								<label for={"variant"+(index+1)} className={ cn('choose-answer') }>{value[index+1]}</label>
							</div>
						)
					})
				}
				<Chat />
			</div>
		)
	}
}

export default Questions


