import React, { Component } from 'react'

class Input extends Component{
	constructor(props){
		super(props)
	}
	render(){
		return(
			<input 
				type={this.props.type} 
				placeholder={this.props.placeholder} 
				className={this.props.className + " input__text"} 
			/>
		)
	}
}

export default Input


