import React, { Component } from 'react'
import cn from 'cn-decorator'
import { Link } from 'react-router-dom'
import Input from '../Common/Input'
import Button from '../Common/Button'

@cn('sign-in')
class SignIn extends Component{
	render(cn){
		return(
			<div className={ cn() }>
				<div className={ cn('header') }>Вход</div>
				<Input 
					type="text" 
					placeholder="Логин или номер телефона"
					className={ cn('input') }
				/>
				<Input 
					type="password" 
					placeholder="Пароль"
					className={ cn('input') }
				/>
				<div className={ cn('button') }>
					<Button type="yellow">ВОЙТИ</Button>
				</div>
				<div className={ cn('acc-control-bar') }>
					<div>Забыли пароль?</div>
					<div>Регистрация</div>
				</div>
				<Link to="/board" className={ cn('watch-live') }>
					<Button type="white">Смотреть эфир без регистрации</Button>
				</Link>
			</div>
		)
	}
}

export default SignIn


