import React, { Component } from 'react'
import cn from 'cn-decorator'

import QuestionsTopBar from './QuestionsTopBar'

@cn('questions')
class RightAnswer extends Component{
	render(cn){
		return(
			<div className={ cn() }>
			<QuestionsTopBar />
			<div className={ cn('question') }>Как назыают людей, которые слишком много спят?</div>
				<div className={ cn('answer-status') }>
					<div className={ cn('answer-status-normal') } style={{'width': '30%'}}>Почемучка</div>
				</div>
				<div className={ cn('answer-status') }>
					<div className={ cn('answer-status-success') } style={{'width': '40%'}}>Почемучка</div>
				</div>
				<div className={ cn('answer-status') }>
					<div className={ cn('answer-status-normal') } style={{'width': '30%'}}>Почемучка</div>
				</div>
			</div>
		)
	}
}

export default RightAnswer


