import React, { Component } from 'react'
import cn from 'cn-decorator'
import { Link } from 'react-router-dom'
import Button from '../Common/Button'

@cn('addition-life')
class AdditionLife extends Component{
	render(cn){
		return(
			<div className={ cn() }>
				<div className={ cn('top-title') }>Дополнительные жизни</div>
				<div className={ cn('life-amount-display') } >
					<i className={ cn('heart') + " icon_heart" }>2</i>
				</div>
				<div className={ cn('description') }>
					С помощью дополнительных жизней вы можете продолжить играть. Приглашайте друзей и знакомых и получате за каждого приглашенного, который сыграет в игру 1 жизнь
				</div>
				<Link to="/board/invite-friends" className={ cn('button') }>
					<Button type="purple-text">Пригласить</Button>
				</Link>
			</div>
		)
	}
}

export default AdditionLife


