import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import cn from 'cn-decorator'

@cn('top-menu')
class TopMenu extends Component{
	constructor(props){
		super(props)
		this.state = {
			winnersStatus: false
		}
	}
	render(cn){
		return(
			<div className={ cn() }>
				<Link 
						to="/board/how-to-play" 
						className={ this.props.history.location.pathname == '/board/how-to-play' ? cn('link') + " " + cn('link-active') : cn('link') }
				>
					<i className="icon_lightning"></i>
					<div>Как играть</div>
				</Link>
				<Link 
					to="/board/faq" 
					className={ this.props.history.location.pathname == '/board/faq' ? cn('link') + " " + cn('link-active') : cn('link') }
				>
					<i className="icon_faq"></i>
					<div>FAQs</div>
				</Link>
				<Link 
					to="/board/winners" 
					className={ this.props.history.location.pathname == '/board/winners' ? cn('link') + " " + cn('link-active') : cn('link') }
				>
					<i className="icon_star-green"></i>
					<div>Победители</div>
				</Link>
			</div>
		)
	}
}

export default TopMenu


