import React, { Component } from 'react'
import cn from 'cn-decorator'


@cn()
class QuestionsTopBar extends Component{
	render(cn){
		return(
			<div className={ cn() }>
				<div className={ cn('members') }>
				 <i className="icon_members"></i>
				 <div>1156</div>
				</div>
				<div className={ cn('mid-status') }>
					<svg width="64" height="64" xmlns="http://www.w3.org/2000/svg">
				    	<g>
				    		<circle id="circle" class={ cn('time') }  r="29" cy="32" cx="32" stroke-width="3" stroke="#694E9F" fill="none"/>
				    	</g>
				    </svg>
				</div>
				<i className={ cn('heart') + " icon_heart" }>2</i>
			</div>
		)
	}
}

export default QuestionsTopBar


