import React, { Component } from 'react'
import Button from '../Common/Button'
import cn from 'cn-decorator'

@cn()
class WrongAnswer extends Component{
	render(cn){
		return(
			<div className={ cn() }>
				<div className={ cn('top-text') }>Вы выбываете</div>
				<div className={ cn('question') }>Как называют людей, которые слишком много спят</div>
				<div className={ cn('answer-status') }>
					<div className={ cn('answer-status-reject') } style={{'width': '40%'}}>Почемучка</div>
				</div>
				<div className={ cn('next-action') }>
					<Button type="purple-text">Поделиться</Button>
					<div className={ cn('separator-30') }></div>
					<Button type="purple-text">Продолжить просмотр</Button>
				</div>
			</div>
		)
	}
}

export default WrongAnswer


