import React, { Component } from 'react'
import cn from 'cn-decorator'

@cn('next-game-wait')
class NextGameWait extends Component{
	render(cn){
		return(
			<div className={ cn() }>
				<div className={ cn('first-title') }>следующая игра</div>
				<div className={ cn('second-title') }>
					<div>Классическая викторина</div>
					<i className="icon_purple-info"></i>
				</div>
				<div className={ cn('third-title') }>Завтра, 20 февраля, в 20:00</div>
				<div className={ cn('prize') }>
					<div className={ cn('prize-first-title') }>Приз</div>
					<div className={ cn('prize-second-title') }>100 000 ₸</div>
				</div>
			</div>
		)
	}
}

export default NextGameWait


