import React, { Component } from 'react'
import cn from 'cn-decorator'
import Phone from 'react-phone-number-input'
import ru from 'react-phone-number-input/locale/ru'
import { Link } from 'react-router-dom'
import Input from '../Common/Input'

import SignIn from './SignIn'


@cn('auth')
class Auth extends Component{
	constructor(props){
		super(props)
		this.state = {
			value: null,
			showInputPhone: false,
			showMassgeCode: false,
		}
	}

	render(cn){
		return(
			<div className={ cn() }>
				<div className={ cn('logo') }></div>
				<div className={ cn('sub-logo') }></div>
				{/*<div className={ cn('reg-title') }>Регистрация</div>*/}
				{this.state.showInputPhone ? 
					<div>
						{/*<Phone
						labels={ ru }
					    placeholder="Ваш телефон"
					    value={ this.state.value }
					    onChange={ value => this.setState({ value }) }
						/>*/}
						<Input 
							type="number" 
							placeholder="Ваш номер телефона" 
							className={ cn('phone') } 
						/>
						<div className={ cn('already-registered') }>У меня уже есть аккаунт. 
							<Link to="/" style={{'color':'inherit'}}>Войти</Link>
						</div>
					</div> 
					: this.state.showMassgeCode ?
					<div>
						<div className={ cn('code-header') }>Введите SMS-код отправленный на номер +77011234567</div>
						<Input 
							type="number"
							placeholder="Введите код из СМС"
							className={ cn('code') }
						/>
						<div className={ cn('send-again') }>Отправить снова через 0:45</div>
					</div>
					: null
				}

				<SignIn />
			</div>
		)
	}
}

export default Auth


