import React, { Component } from 'react'
import Button from '../Common/Button'
import cn from 'cn-decorator'

@cn('win-page')
class WinPage extends Component{
	render(cn){
		return(
			<div className={ cn() }>
				<div className={ cn('top-text') }>Вы выиграли</div>
				<div className={ cn('gain') }>Выигрыш</div>
				<div className={ cn('gain-amount') }>5000 ₸</div>
				<div className={ cn('congrt') }>Поздравляем! <br /> Можете рассказать об этом друзьям!</div>
				<div className={ cn('buttons') } >
					<div className={ cn('button') }>
						<Button type="purple-transparent">Поделиться</Button>
					</div>
					<div className={ cn('button') }>
						<Button type="purple-text">Продолжить</Button>
					</div>
				</div>
			</div>
		)
	}
}

export default WinPage


