import React, { Component } from 'react'
import cn from 'cn-decorator'
import Button from '../components/Common/Button'
import ScrollArea from 'react-scrollbar'
import { Switch, Route } from 'react-router'
import { Link } from 'react-router-dom'

import TopMenu from '../components/Board/TopMenu'
import AccountInfo from '../components/Board/AccountInfo'
import NextGameWait from '../components/Board/NextGameWait'
import Questions from '../components/Board/Questions'
import AdditionLife from '../components/Board/AdditionLife'
import SocHistory from '../components/Board/SocHistory'
import WinPage from '../components/Board/WinPage'
import Winners from '../components/Board/Winners'
import HowToPlay from '../components/Board/HowToPlay'
import FAQ from '../components/Board/FAQ'
import SingleNews from '../components/Board/SingleNews'
import InviteFriends from '../components/Board/InviteFriends'
import WrongAnswer from '../components/Board/WrongAnswer'
import RightAnswer from '../components/Board/RightAnswer'

import avatar from '../assets/img/ava.jpg'
import person1 from '../assets/img/person1.jpg'
import person2 from '../assets/img/person2.jpg'
import person3 from '../assets/img/person3.jpg'
import person4 from '../assets/img/person4.jpeg'

import $ from 'jquery'
import { doResize } from '../components/Board/ScaleResize'




@cn('board')
class Board extends Component {
	constructor(props){
		super(props)
		this.state = {
			showNextGameWait: false,
			size: {
			    width: $('.board').width(),
			    height: $('.board').height()
			},
		}
	}

	render(cn){
		const el = $('.board')
		const elHeight = el.outerHeight()
		const elWidth = el.outerWidth()
		doResize(null, this.state, el, elWidth, elHeight)
		return(
			<div className={ cn() }>
				<div className={ cn('logo') }></div>
				<div className={ cn('panel') } >
					<div className={ cn('panel-left') }>
						<AccountInfo />
						<div className={ cn('panel-left-block') }>
							<div className={ cn('gray-text') }>выйгрыш</div>
							<div className={ cn('purple-text') }>5000 ₸</div>
							<div className={ cn('green-text') }>+ 500 ₸</div>
						</div>
						<div className={ cn('panel-left-block') }>
							<div className={ cn('gray-text') }>твой ранк</div>
							<div className={ cn('purple-text') }>152</div>
							<div className={ cn('purple-text-small') }>Побед: 24</div>
						</div>
						<div className={ cn('panel-left-two-buttons') }>
							<Button type={ "purple-text " + cn('purple-text-button') }>
								<Link to="/board/winners" style={{'text-decoration': 'inherit'}}>Победители</Link>
							</Button>
							<Button type="purple-text">
								<Link to="/board/invite-friends" style={{'text-decoration': 'inherit'}}>Пригласить</Link>
							</Button>
						</div>
						<div className={ cn('panel-left-soc') }>
							<SocHistory statusType="disabled" avatar={person1}/>
							<SocHistory statusType="active" avatar={avatar}/>
							<SocHistory statusType="active" avatar={person2}/>
							<SocHistory statusType="active" avatar={person3}/>
							<SocHistory statusType="active" avatar={person4}/>
						</div>
						<div className={ cn('logout-button') }>
							<Button type="white">Выйти</Button>
						</div>
						<div className={ cn('panel-left-soc') }>
							<div className={ cn('panel-left-soc-links') }>
								<i className="icon_twitter"></i>
								<i className="icon_instagram"></i>
								<i className="icon_facebook"></i>
							</div>
							<div className={ cn('panel-left-soc-links-text') }>
								Правила конфиденциальности
							</div>
						</div>
					</div>
					<TopMenu history={this.props.history}/>
					<div className={ cn('panel-right') }>
						
						<div className={ cn('content') }>
							<Switch>
							  <Route exact path="/board" component={NextGameWait}/>
							  <Route path="/board/addition-life" component={AdditionLife}/>
							  <Route path="/board/questions" component={Questions} />
							  <Route path="/board/win-page" component={WinPage}/>
							  <Route path="/board/winners" component={Winners}/>
							  <Route path="/board/how-to-play" component={HowToPlay}/>
							  <Route path="/board/faq" component={FAQ}/>
							  <Route path="/board/single-news" component={SingleNews}/>
							  <Route path="/board/invite-friends" component={InviteFriends}/>
							  <Route path="/board/wrong-answer" component={WrongAnswer}/>
							  <Route path="/board/right-answer" component={RightAnswer}/>
							</Switch>
						</div>
					</div>
				</div>	
			</div>
		)
	}
}

export default Board


