export const doResize = (event, ui, el, elWidth, elHeight) => {
  
  var scale, origin;
    
  scale = Math.min(
    ui.size.width / elWidth,    
    ui.size.height / elHeight
  );
  
  el.css({
    transform: "translate(-50%, -50%) " + "scale(" + scale + ")"
  });
  
}

