import React, { Component } from 'react'
import cn from 'cn-decorator'
import Button from '../Common/Button'
import { Link } from 'react-router-dom'

import avatar from '../../assets/img/ava.jpg'

@cn('account-info')
class AccountInfo extends Component{
	render(cn){
		return(
			<div className={ cn() }>
				<div className={ cn('avatar-wrap') }>
					<div className={ cn('avatar') }>
						<img src={avatar} className={ cn('avatar-img') }/>
					</div>
					<i className={ cn('heart') + " icon_heart"}>2</i>
				</div>
				<div className={ cn('acc-control') }>
					<div className={ cn('nickname') }>YourNickName</div>
					<div className={ cn('button') }>
						<Link to="/board/addition-life" target="_self"><Button type="white">Добавть больше жизней</Button></Link>
					</div>
				</div>
			</div>
		)
	}
}

export default AccountInfo



