import React, { Component } from 'react'
import Button from '../Common/Button'
import cn from 'cn-decorator'

@cn()
class InviteFriends extends Component{
	render(cn){
		return(
			<div className={ cn() }>
				<div className={ cn('top-title') }>Дополнительные жизни</div>
				<div className={ cn('life-amount-display') } >
					<i className={ cn('heart') + " icon_heart" }>2</i>
				</div>
				<div className={ cn('description') }>
					За каждого друга +1 жизнь, после того как он зарегистрируется
				</div>
				<div className={ cn('soc-bar') }>
					<div className={ cn('vk') }>
						<i className="icon_vk-white"></i>Поделиться
					</div>
					<div className={ cn('twitter') }>
						<i className="icon_twitter-white"></i>Твитнуть
					</div>
					<div className={ cn('fb') }>
						<i className="icon_fb-white"></i>Поделиться
					</div>
				</div>
				<div className={ cn('button') }>
					<Button type="purple-text">Пригласить</Button>
				</div>
			</div>
		)
	}
}

export default InviteFriends


