import React, { Component } from 'react'
import cn from 'cn-decorator'
import avatar from '../../assets/img/ava.jpg'
import person1 from '../../assets/img/person1.jpg'
import person2 from '../../assets/img/person2.jpg'
import person3 from '../../assets/img/person3.jpg'
import person4 from '../../assets/img/person4.jpeg'

@cn('winners')
class Winners extends Component{
	constructor(props){
		super(props)
		this.state = {
			showWeek: true,
			showMonth: false
		}
	}
	render(cn){
		return(
			<div className={ cn() }>
				<div className={ cn('top-text') }>Победители</div>
				<div className={ cn('time-handler') }>
					<div 
						className={ this.state.showWeek ? cn('week') + " " + cn('time-active') : cn('week') } 
						onClick={() => this.setState({...this.state, showWeek:true, showMonth:false})}>За неделю
					</div>
					<div 
						className={ this.state.showMonth ? cn('month') + " " + cn('time-active') : cn('month') }
						onClick={() => this.setState({...this.state, showWeek:false, showMonth:true})}>За месяц
					</div>
				</div>
				{
					this.state.showWeek ?
					<div>
						<div className={ cn('top') }>
							<div className={ cn('top-item') }>
								<div className={ cn('top-ava-wrapper') }>
									<div className={ cn('top-ava') }>
										<img src={avatar} style={{'width':'100%'}}/>
									</div>
									<div className={ cn('top-ava-place') }>2</div>
								</div>
								<div className={ cn('top-name') }>Elon Musk</div>
								<div className={ cn('top-gain') }>5000 ₸</div>
							</div>
							<div className={ cn('top-item') }>
								<div className={ cn('top-ava-wrapper') }>
									<div className={ cn('top-ava') }>
										<img src={person1} style={{'width':'100%'}}/>
									</div>
									<div className={ cn('top-ava-place1') }>1</div>
								</div>
								<div className={ cn('top-name') }>Username</div>
								<div className={ cn('top-gain') }>5000 ₸</div>
							</div>
							<div className={ cn('top-item') }>
								<div className={ cn('top-ava-wrapper') }>
									<div className={ cn('top-ava') }>
										<img src={person2} style={{'width':'100%'}}/>
									</div>
									<div className={ cn('top-ava-place') }>3</div>
								</div>
								<div className={ cn('top-name') }>Username</div>
								<div className={ cn('top-gain') }>5000 ₸</div>
							</div>
						</div>
						<div className={ cn('others') }>
							<div className={ cn('others-item') }>
								<div className={ cn('others-num') }>4</div>
								<div className={ cn('others-ava') }><img src={avatar} style={{'width':'100%'}}/></div>
								<div className={ cn('others-name') }>Username</div>
								<div className={ cn('others-gain') }>5000 ₸</div>	
							</div>
							<div className={ cn('others-item') }>
								<div className={ cn('others-num') }>5</div>
								<div className={ cn('others-ava') }><img src={person4} style={{'width':'100%'}}/></div>
								<div className={ cn('others-name') }>Username</div>
								<div className={ cn('others-gain') }>5000 ₸</div>	
							</div>
							<div className={ cn('others-item') }>
								<div className={ cn('others-num') }>6</div>
								<div className={ cn('others-ava') }><img src={person3} style={{'width':'100%'}}/></div>
								<div className={ cn('others-name') }>Username</div>
								<div className={ cn('others-gain') }>5000 ₸</div>	
							</div>
						</div>
					</div>
					:this.state.showMonth ?
					<div>
						<div className={ cn('top') }>
							<div className={ cn('top-item') }>
								<div className={ cn('top-ava-wrapper') }>
									<div className={ cn('top-ava') }>
										<img src={person2} style={{'width':'100%'}}/>
									</div>
									<div className={ cn('top-ava-place') }>2</div>
								</div>
								<div className={ cn('top-name') }>Username</div>
								<div className={ cn('top-gain') }>5000 ₸</div>
							</div>
							<div className={ cn('top-item') }>
								<div className={ cn('top-ava-wrapper') }>
									<div className={ cn('top-ava') }>
										<img src={person4} style={{'width':'100%'}}/>
									</div>
									<div className={ cn('top-ava-place1') }>1</div>
								</div>
								<div className={ cn('top-name') }>Username</div>
								<div className={ cn('top-gain') }>5000 ₸</div>
							</div>
							<div className={ cn('top-item') }>
								<div className={ cn('top-ava-wrapper') }>
									<div className={ cn('top-ava') }>
										<img src={person1} style={{'width':'100%'}}/>
									</div>
									<div className={ cn('top-ava-place') }>3</div>
								</div>
								<div className={ cn('top-name') }>Username</div>
								<div className={ cn('top-gain') }>5000 ₸</div>
							</div>
						</div>
						<div className={ cn('others') }>
							<div className={ cn('others-item') }>
								<div className={ cn('others-num') }>4</div>
								<div className={ cn('others-ava') }><img src={person4} style={{'width':'100%'}}/></div>
								<div className={ cn('others-name') }>User</div>
								<div className={ cn('others-gain') }>5000 ₸</div>	
							</div>
							<div className={ cn('others-item') }>
								<div className={ cn('others-num') }>5</div>
								<div className={ cn('others-ava') }><img src={avatar} style={{'width':'100%'}}/></div>
								<div className={ cn('others-name') }>Elon Musk</div>
								<div className={ cn('others-gain') }>5000 ₸</div>	
							</div>
							<div className={ cn('others-item') }>
								<div className={ cn('others-num') }>6</div>
								<div className={ cn('others-ava') }><img src={person2} style={{'width':'100%'}}/></div>
								<div className={ cn('others-name') }>Username</div>
								<div className={ cn('others-gain') }>5000 ₸</div>	
							</div>
						</div>
					</div>
					: null
				}
				
			</div>
		)
	}
}

export default Winners


