import React, { Component } from 'react'
import cn from 'cn-decorator'


@cn()
class HowToPlay extends Component{
	constructor(props){
		super(props)
		this.state = {
			arr: [1,2,3,4,5,6]
		}
	}
	render(cn){
		return(
			<div className={ cn() }>
			{
				this.state.arr.map(() => {
					return(
						<div className={ cn('item') }>
							<div className={ cn('item-play') }></div>
							<div className={ cn('item-text') }>
								<div className={ cn('main-text') }>Для начала, войдите в игру</div>
								<div className={ cn('sub-text') } >Мы отправим вам уведомление. Не опаздывайте!</div>
							</div>
						</div>
					)

				})
			}
			</div>
		)
	}
}

export default HowToPlay


