import React, { Component } from 'react'
import cn from 'cn-decorator'

@cn('board__panel-left-soc')
class SocHistory extends Component{
	constructor(props){
		super(props)
	}

	render(cn){
		const statusType = this.props.statusType
		return(
			<div className={ cn(`item-${statusType}`)}>
				<img src={this.props.avatar} style={{'width':'100%'}}/>
			</div>
		)
	}
}

export default SocHistory


